$showTraitDuration: 2.5;
$moveTraitAmountPixels: 40;
$navHeight: 3rem;

$danger: #ef476f;
$dangerFaded: rgba(239, 71, 111, 0.5); // #EF476F;
$dangerVeryFaded: rgba(239, 71, 111, 0.2); // #EF476F;
$success: #06d6a0;
$successFaded: rgba(6, 214, 160, 0.4); // #06D6A0;
$warning: #ffd166;
$warningFaded: rgba(255, 209, 102, 0.9); // #FFD166;;
$warningVeryFaded: rgba(255, 209, 102, 0.3); // #FFD166;;
$info: #118ab2;
$infoFaded: rgba(17, 138, 178, 0.2);
$dark: #363636;
$light: #073b4c;

@keyframes fadeInOut {
	0% {
		opacity: 0;
	}
	16% {
		opacity: 1;
	}
	84% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@keyframes fadeOut {
	0% {
		opacity: 1;
		top: 0px;
	}

	90% {
		opacity: 0.8;
		// top: -#{$moveTraitAmountPixels * 0.0}px;
	}

	100% {
		opacity: 0;
		top: -#{$moveTraitAmountPixels}px;
	}
}

body {
	padding-top: 2.5rem !important;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;

	.traitName {
		font-weight: normal !important;
		font-family: inherit;
		font-size: inherit;
	}

	#statsView {
		.statsHeader {
			min-height: $navHeight;
			max-height: $navHeight;
		}
		.title {
			display: inline-flex;
			align-items: center;
		}
		.quickview-footer {
			min-height: 3rem;
		}

		#upgradesBought {
			ul {
				min-height: 3.75rem;
				line-height: 1.3 !important;
				max-height: 3.75rem;
				text-align: left;
				overflow-y: scroll;
				overflow-x: visible;
				z-index: 9999999;
				li {
