						align-items: center;
						padding: 0.3rem 2rem;
						width: 100%;
						text-align: left;
					}

					.buttonContainer {
						display: flex;
						button {
							height: auto;
							padding-left: 2.3rem;
							padding-right: 2.3rem;
						}

						.mainButton {
							min-width: 9.5rem;
						}
					}
				}

				.card-footer {
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding: 0.2rem 1rem;

					.pauseIcon {
