				}
			}

			&.production {
				.progressContainer {
					height: 100%;
					display: flex;
					width: 100%;
					align-items: flex-start;
					flex-direction: column;
					flex-grow: 1;
					flex-shrink: 1;
				}
			}

			&.justFailed {
				background: $dangerVeryFaded !important;
				box-shadow: 0 1px 2px $danger, 0 0 0 1px $danger;
			}

			&.employees {
			}

