					margin-top: 0.5rem;
				}
			}
		}

		.traitBox {
			padding-top: 0;
			// max-height: 85vh;

			&.full {
				// background: lightcoral;
				//font-size: 90%;
				box-shadow: 0 1px 2px $danger, 0 0 0 1px $danger;
			}

			button.massDelivery {
				float: right;
				top: -14px;
				margin-right: 0.1rem;
			}

			.subtitle {
				// &.storageAmount{
				// 	font-size: 80%;
				// 	vertical-align: bottom;
				// }

				&.full {
					color: $danger;
					//	background: lightcoral !important;
				}
			}

