				height: 40px;
				align-items: center;
				justify-content: space-between;

				.imgContainer {
					min-width: 50px;
					text-align: center;
				}

				img {
					position: relative;
					top: 2px;
					margin-right: 10px;
					max-height: 32px;
				}

				.traitName {
					text-transform: uppercase;
					font-variant: small-caps;
					font-variant-caps: all-petite-caps;
