/// FACTORY VISUALS ///

@keyframes powerUsed {
	0% {
		top: 0px;
	}
	50% {
		top: -12px;
	}
	100% {
		top: 0px;
	}
}

@keyframes prodWorkingRotate {
	0% {
		transform: rotate(-20deg);
	}

	50% {
		transform: rotate(20deg);
	}

	100% {
		transform: rotate(-20deg);
	}
}

@keyframes storageFull {
	0% {
		top: 0px;
		font-size: 110%;
		transform: rotate(0deg);
	}

	85% {
		top: 0px;
		font-size: 110%;
		transform: rotate(0deg);
	}

	90% {
		top: -5px;
		font-size: 115%;
		transform: rotate(20deg);
	}

	95% {
		top: -5px;
		font-size: 115%;
		transform: rotate(-20deg);
	}

	100% {
		transform: rotate(0deg);
		font-size: 110%;
		top: 0px;
	}
}

@keyframes prodWorkingScale {
	0% {
		font-size: 150%;
	}

	40% {
		font-size: 155%;
	}

	50% {
		font-size: 170%;
	}

	60% {
		font-size: 155%;
	}

	100% {
		font-size: 150%;
	}
}

@keyframes delivering {
	0% {
		top: -1rem;
	}

	10% {
		top: -1rem;
	}

	40% {
		top: 4.5rem;
	}

	60% {
		top: 4.5rem;
	}

	90% {
		top: -1rem;
	}

	100% {
		top: -1rem;
	}
}

#factoryVisual {
	.outputIndicator {
		margin-top: 1rem !important;
		margin-bottom: 1rem !important;
		width: 100%;
		text-align: center;
		display: inline-flex;
		align-items: center;
		justify-content: center;
	}

	#currentJarOutput {
		font-variant-caps: all-small-caps;
		width: 100%;
		margin-top: 0 !important;
		margin-bottom: 1rem;
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 2rem;
		background: #fafafa;
		.appearingJar {
			-webkit-animation: swashIn forwards;
			animation: swashIn forwards;
		}
		img {
			height: 25px;
		}
	}

	#allBuildings {
		display: flex;
		flex-direction: column;
		justify-content: center;

		#prodBuildings {
			margin-top: 0;
			margin-bottom: 0;

			.manualWorker {
				&.working {
					color: $success;
				}
				&.notWorking {
					color: $light;
				}
			}

			.prodBuilding {
				font-size: 150%;
				margin: 0 0.2rem;
				min-height: 2rem;
				max-height: 2rem;
				min-width: 1rem;
				max-width: 1rem;
			}
			.buildingAllocated {
				color: $success;
				-webkit-animation: prodWorkingScale 1s ease-in-out infinite;
				animation: prodWorkingScale 1s ease-in-out infinite;
			}
			.buildingUnallocated {
				color: $danger;
			}
		}

		.visualBuildings {
			display: flex;
			align-items: center;
			justify-content: space-evenly;
			flex-wrap: wrap;

			.building {
				text-shadow: 1px 0px #333;
			}
		}

		#deliveryPeople {
			min-height: 10rem;

			.delivery {
				-webkit-animation: delivering 10s ease-in-out infinite;
				animation: delivering 10s ease-in-out infinite;

				&.is-delivery-success {
					color: $success;
				}
				&.is-delivery-warning {
					color: $warning;
				}
				&.is-delivery-danger {
					color: $danger;
				}
			}
		}

		#powerBuildings {
			.powerBuilding {
				font-size: 150%;
				margin: 0 0.3rem;
				color: $dangerFaded;
				position: relative;

				&.isUsed {
					color: $warning;
					-webkit-animation: powerUsed 2s ease-in-out infinite;
					animation: powerUsed 2s ease-in-out infinite;
				}
			}
		}

		#storageBuildings {
			.storageBuilding {
				position: relative;
				font-size: 110%;
				margin: 0 0.3rem;
				color: $successFaded;
				min-height: 1.5rem;
				max-height: 1.5rem;
				min-width: 1.1rem;
				&.isFull {
					color: $warningFaded;
					-webkit-animation: storageFull 2s ease-in-out infinite;
					animation: storageFull 2s ease-in-out infinite;
				}
			}
		}
	}
}

/* FIREWORKS */

$particles: 100;
$width: 500;
$height: 500;

// Create the explosion...
$box-shadow: ();
$box-shadow2: ();
@for $i from 0 through $particles {
	$box-shadow: $box-shadow,
		random($width)-$width /
			2 +
			px
			random($height)-$height /
			1.2 +
			px
			hsl(random(360), 100, 50);
	$box-shadow2: $box-shadow2, 0 0 #fff;
}
@mixin keyframes($animationName) {
	@-webkit-keyframes #{$animationName} {
		@content;
	}

	@-moz-keyframes #{$animationName} {
		@content;
	}

	@-o-keyframes #{$animationName} {
		@content;
	}

	@-ms-keyframes #{$animationName} {
		@content;
	}

	@keyframes #{$animationName} {
		@content;
	}
}

@mixin animation-delay($settings) {
	-moz-animation-delay: $settings;
	-webkit-animation-delay: $settings;
	-o-animation-delay: $settings;
	-ms-animation-delay: $settings;
	animation-delay: $settings;
}

@mixin animation-duration($settings) {
	-moz-animation-duration: $settings;
	-webkit-animation-duration: $settings;
	-o-animation-duration: $settings;
	-ms-animation-duration: $settings;
	animation-duration: $settings;
}

@mixin animation($settings) {
	-moz-animation: $settings;
	-webkit-animation: $settings;
	-o-animation: $settings;
	-ms-animation: $settings;
	animation: $settings;
}

@mixin transform($settings) {
	transform: $settings;
	-moz-transform: $settings;
	-webkit-transform: $settings;
	-o-transform: $settings;
	-ms-transform: $settings;
}

.pyro > .before,
.pyro > .after {
	position: absolute;
	width: 5px;
	height: 5px;
	border-radius: 50%;
	box-shadow: $box-shadow2;
	@include animation(
		(
			1s bang ease-out infinite backwards,
			1s gravity ease-in infinite backwards,
			5s position linear infinite backwards
		)
	);
}

.pyro > .after {
	@include animation-delay((1.25s, 1.25s, 1.25s));
	@include animation-duration((1.25s, 1.25s, 6.25s));
}

@include keyframes(bang) {
	to {
		box-shadow: $box-shadow;
	}
}

@include keyframes(gravity) {
	to {
		@include transform(translateY(200px));
		opacity: 0;
	}
}

@include keyframes(position) {
	0%,
	19.9% {
		margin-top: 10%;
		margin-left: 40%;
	}
	20%,
	39.9% {
		margin-top: 40%;
		margin-left: 30%;
	}
	40%,
	59.9% {
		margin-top: 20%;
		margin-left: 70%;
	}
	60%,
	79.9% {
		margin-top: 30%;
		margin-left: 20%;
	}
	80%,
	99.9% {
		margin-top: 30%;
		margin-left: 80%;
	}
}

